@import '/src/assets/styles/mixins';

.box {
  display: flex;
  align-items: center;

  @include mq(mob-only) {
    margin-top: 20px;
  }
  @include mq(desk) {
    margin-top: 586px;
  }
}

.textBox {
  display: flex;
  flex-direction: column;
  margin-left: 12px;
}

.name {
  font-weight: 500;
  font-size: 14px;
  line-height: calc(21 / 14);

  letter-spacing: 0.01em;

  color: #000000;
}

.profession {
  font-size: 12px;
  line-height: calc(18 / 12);

  letter-spacing: 0.01em;

  color: #757575;
}
